/*eslint-disable*/

import React, { Component } from 'react';

export default class Pill extends Component {
	render() {
		switch(this.props.site){
			case "ecsdata":
				return (
					<div className="g2">
						<div><img src={'http://ecstasydata.org'+this.props.photo} style={{width:'75px', height:'75px'}} /></div>
						<div><p>{this.props.name}</p></div>
						<div><p>{this.props.substance}</p></div>
						<div><p>{this.props.location}</p></div>
						<div><a target="_blank" href={'http://ecstasydata.org/'+this.props.link}><p>More info</p></a> </div>
					</div>
				);
				break;
			case "pillrep":
				return (
					<div className="g2">
						<div><img src={'http://pillreports.net/'+this.props.photo} style={{width:'75px', height:'75px'}} /></div>
						<div><p>{this.props.name}</p></div>
						<div><p>{this.props.substance}</p></div>
						<div><p>{this.props.location}</p></div>
						<div><a target="_blank" href={'http://pillreports.net/'+this.props.link}><p>More info</p></a> </div>
					</div>
				);
				break;
			default:
				return (
					<div className="g2">
						<div><img src="#" style={{width:'75px', height:'75px'}} /></div>
						<div><p>ERROR</p></div>
						<div><p>ERROR</p></div>
						<div><p>ERROR</p></div>
						<div><a href="#"><p>ERROR</p></a> </div>
					</div>
				);
			}
		}
	}




