import React, { Component } from 'react';

export default class Dropdown extends Component {
	render() {
		return (
			<div className="dropdown-div">
				<h2>Original websites</h2>
				<div className="dropdown-links">
					<a href="http://pillreports.net" target="_blank" rel="noopener noreferrer"><p>pillreports.net</p></a>
					<a href="http://ecstasydata.org" target="_blank" rel="noopener noreferrer"><p>ecstasydata.org</p></a>
					<a href="http://drogart.org" target="_blank" rel="noopener noreferrer"><p>drogart.org</p></a>
				</div>
			</div>
		);
	}
}



