/*eslint-disable*/

import React, { Component } from 'react';
import $ from 'jquery';

import Dropdown from './Dropdown';
import Choise from './Choise';
import Pill from './Pill';
import Choise2 from './Choise2';

export default class Search extends Component {

	componentDidMount(){
		if (/Android|webOS|iPhone|iPad|iPod|BlackBerry|BB|PlayBook|IEMobile|Windows Phone|Kindle|Silk|Opera Mini/i.test(navigator.userAgent)) {
    		$('.dropdown-links').css({display:'flex',flexDirection:'column', alignItems:'center',boxShadow:'0px 8px 16px 0px rgba(0,0,0,0.2)'});
		}
	}

	componentDidUpdate(){
		if(this.state.search){
			this.setState({search:0})
			this.props.func(this.state.comp)
		}
	}

	getData(){
		let tmp = [];
		let components = [];

		const cheerio = require('cheerio');
		let logo = $('#logo').val().split(' ').join('+');
		let color = "";
		let color1 = "";
		if($('#color').val()!=0){
			if($('#color').val()!=18){
				color = $('#color').val();
			}
			color1 = $('#color option:selected').text();
		}
		let country;
		let country1;
		if($('#country').val()!=0){
			country = $('#country').val();
			country1 = $('#country option:selected').text();
		}else{
			country = "";
			country1 = "";
		}
		


		$.getJSON('http://anyorigin.com/go?url=https%3A//www.ecstasydata.org/search.php%3Fsearch_field%3Dadv%26name%3D'+logo+'%26field_test%3D%26substance1%3D%26substance2%3D%26id%3D%26color%3D'+color+'%26colorexact%3D0%26city%3D%26source%3D%26m1%3D%26y1%3D%26m2%3D%26y2%3D%26state%3D%26country%3D'+country+'%26sold_as_ecstasy%3Dboth&callback=?', (data)=>{
			const eData = cheerio.load(data.contents);
			tmp.push(eData('.even'));
			tmp.push(eData('.odd'));

			for(let j=0;j<2;j++){
				for(let i=0;i<tmp[j].length;i++){
					components.push(<Pill photo={tmp[j][i].children[1].children[0].children[0].attribs.src}
					 						name={tmp[j][i].children[3].children[0].children[0].data}
					 						substance={tmp[j][i].children[5].children[1].children[1].children[0].data}
					 						location={tmp[j][i].children[13].children[0].data}
					 						link={tmp[j][i].children[1].children[0].attribs.href}
					 						site="ecsdata"
					 						key={i+j*100}
					  />)
				}
			}
			this.setState({comp:components, search:1});
		})
		components = [];
		$.getJSON('http://anyorigin.com/go?url=https%3A//www.pillreports.net/index.php%3Fpage%3Dsearch_reports%26sent%3D1%26username%3D%26name%3D'+logo+'%26logo%3D%26colour%3D'+color1+'%26state%3D'+country1+'%26region%3Dall%26suspected_contents%3Dall%26rating%3Dall%26pp%3D10&callback=?', (data)=>{
			const pData = cheerio.load(data.contents);
			const reports = pData('.search-reports');
			if(reports[0]!=undefined){
				for(let i=0;i<(reports[0].children.length-1)/2;i++){
					if(reports[0].children[2*i+1].children[1].children[7].children[1].children[1].children[1].children[0].attribs.src!==undefined){
						components.push(<Pill photo={reports[0].children[2*i+1].children[1].children[7].children[1].children[1].children[1].children[0].attribs.src}
							 						name={reports[0].children[2*i+1].children[1].children[1].children[0].children[0].data}
							 						substance={reports[0].children[2*i+1].children[1].children[7].children[3].children[1].children[1].children[1].children[3].children[0].data}
							 						location={reports[0].children[2*i+1].children[1].children[5].children[1].children[1].children[2].children[0].data}
							 						link={reports[0].children[2*i+1].children[1].children[7].children[1].children[1].children[1].attribs.href}
							 						site="pillrep"
							 						key={i+10000}
							  />)
					}else{
						components.push(<Pill photo={reports[0].children[2*i+1].children[1].children[7].children[1].children[1].children[1].children[1].attribs.src}
							 						name={reports[0].children[2*i+1].children[1].children[1].children[0].children[0].data}
							 						substance={reports[0].children[2*i+1].children[1].children[7].children[3].children[1].children[1].children[1].children[3].children[0].data}
							 						location={reports[0].children[2*i+1].children[1].children[5].children[1].children[1].children[2].children[0].data}
							 						link={reports[0].children[2*i+1].children[1].children[7].children[1].children[1].children[1].attribs.href}
							 						site="pillrep"
							 						key={i+10000}
							  />)
					}
				}
			}
			if(this.state.comp.length==0 && components.length==0){
				this.setState({comp:[<Pill photo=""
					 						name="No"
					 						substance="Pills"
					 						location="Found"
					 						link=""
					 						site="pillrep"
					 						key="-1"
					  />], search:1});
			}else{
				this.setState({comp:this.state.comp.concat(components)});
			}
			
		});
   

			
	}


	constructor(props){
		super(props);
		this.getData = this.getData.bind(this);
		this.state = {comp:[], search:0}
	}


	render() {
		return (
			<div className="search-div">
				<div className="g1">
					<div id="searchDiv" className="form-div">
						<h2>Logo</h2>
						<input type="text" id="logo" />
					</div>
					<div id="searchDiv" className="flex flex-center v-start">
						<Dropdown/>
					</div>
					<div id="searchDiv" className="form-div">
						<h2>Color</h2>
						<Choise/>
					</div>
					<div id="searchDiv" className="form-div">
						<h2>Country</h2>
						<Choise2/>
					</div>
					<div id="searchDiv" className="flex flex-center v-end">
					</div>
					<div id="searchDiv" className="flex v-align flex-center">
						<input type="button" value="Search" onClick={ this.getData } />
					</div>
				</div>
			</div>
		);
	}
}



