import React, { Component } from 'react';

import Content from './Content';
import Results from './Results';

export default class App extends Component {

  showRes(results){
    this.setState({res:results});
  }

  constructor(props){
    super(props);
    this.state = {res:[]}

    this.showRes = this.showRes.bind(this);
  }

  render() {
      return (
        <div>
          <h1 className="text-center" style={{color:'gray'}} >Pill Report Hub</h1>
          <Content func={this.showRes}/>
          <Results res={this.state.res}/>
        </div>
      );
  }
}




